import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox
	},
	data() {
		return {
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			banner: [
				"https://www.thorui.cn/img/product/11.jpg",
				"https://www.thorui.cn/img/product/2.png",
				"https://www.thorui.cn/img/product/33.jpg",
				"https://www.thorui.cn/img/product/4.png",
				"https://www.thorui.cn/img/product/55.jpg",
				"https://www.thorui.cn/img/product/6.png",
				"https://www.thorui.cn/img/product/7.jpg",
				"https://www.thorui.cn/img/product/8.jpg"
			],
			bannerIndex: 0,
			topMenu: [{
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, 
			// {
			// 	icon: "message",
			// 	text: "消息",
			// 	size: 26,
			// 	badge: 3
			// },
			{
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, 
			{
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}, 
			// {
			// 	icon: "kefu",
			// 	text: "客服小蜜",
			// 	size: 26,
			// 	badge: 0
			// }, 
			// {
			// 	icon: "feedback",
			// 	text: "我要反馈",
			// 	size: 23,
			// 	badge: 0
			// }, 
			{
				icon: "share",
				text: "分享",
				size: 26,
				badge: 0
			}],
			menuShow: false,
			popupShow: false,
			value: 1,
			collected: false
		}
	},
	onLoad: function(options) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();








		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
	},
	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.banner[index],
				urls: this.banner
			})
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		showPopup: function() {
			this.popupShow = true
		},
		hidePopup: function() {
			this.popupShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		// collecting: function() {
		// 	this.collected = !this.collected
		// },
		common: function() {
			this.tui.toast("功能开发中~")
		},
		linkToCar(){
			uni.reLaunch({
				url: '/pages/car/car'
			})
		}
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
